//待办及提醒事项查看全部详情
import { Component, OnInit, Inject, forwardRef, OnChanges } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
@Component({
	selector: 'app-personal-do',
	templateUrl: './personal-do.component.html',
	styleUrls: ['./personal-do.component.scss'],
	providers: [Commfunc]
})
export class PersonaldoComponent implements OnInit, OnChanges {
	custName: any;
	custNo: any;
	user: any;
	detailList: any;
	public show: any = 1;
	constructor(
		public commfunc: Commfunc,
		public router: Router,
		public activatedRouter: ActivatedRoute,
		public routeInfo: ActivatedRoute,
	) {
	}


	ngOnInit() {
		this.activatedRouter.params.subscribe(param => {
			this.custName = param.custName;
			this.custNo = param.custNo;
			if (param.show) {
				this.show = param.show 
			}
		});
		this.judgStatu();
	}
	ngOnChanges() {
		this.ngOnInit();
	}
	apply(num) {
		this.show = num;
	}
	judgStatu() {
		let url = window.location.href
		if (url.indexOf('?') != -1) {
			let a = url.split('?');
			let b = a[1].split('&');
			let c = b[0].split('=');
			this.show = c[1];
		}
	}
	//按钮权限
	permissionCheck(id) {
		return this.commfunc.permissionCheck(id);
	}
	ngOnDestory() {
		if (this.commfunc.getSessionDataCH('bussinessWhere')) {
			this.commfunc.clearSession('bussinessWhere');
		}
	}
}
