import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { PersonaldoComponent } from './personal-do/personal-do.component';

export const routes: Routes = [

  {
    path: '',
    children: [
      {
        path: 'backlog-detail',
        loadChildren: './backlog-detail/backlog.detail.module#BacklogDetailModule'
      },
      {
        path: 'backlog-details',
        loadChildren: './backlog-detail/backlog.detail.module#BacklogDetailModule'
      },
      {
        path: 'personal-do',
        component: PersonaldoComponent
      }
    ]
  }

]

export const ReminderDoRouting: ModuleWithProviders = RouterModule.forChild(routes);
